import React, { Component } from "react";
import TodoItem from "./TodoItem";
import { Link } from "react-router-dom";
import { logout, deleteTodo } from "./../utils/apiFeatures";

class TodoList extends Component {
  state = {
    todoList: [],
    count: 0,
  };

  handleLogout = () => {
    logout();
    this.props.history.push("/home");
  };

  handleDel = (id) => {
    deleteTodo(id, localStorage.getItem("jwt"));
    const newTodos = this.state.todoList.filter((el) => el._id !== id);
    this.setState({ todoList: newTodos });
  };

  UNSAFE_componentWillMount() {
    fetch(`https://desolate-reef-21809.herokuapp.com/api/v1/todos`, {
      headers: {
        Authorization: `Bearer ${localStorage.getItem("jwt")}`,
      },
    })
      .then((data) => data.json())
      .then((result) => {
        this.setState({ todoList: result.data.todos, count: result.results });
      });
  }

  render() {
    return (
      <div className='d-flex justify-content-center'>
        <div className='card mt-5' style={{ width: "30rem" }}>
          <h6 className='card-header text-right'>
            <button
              className='btn btn-outline-danger float-right'
              onClick={this.handleLogout}
            >
              Logout
            </button>
          </h6>
          <h5 className='card-header text-center'>Todos</h5>
          <ul className='list-group list-group-flush'>
            {this.state.todoList.map((todo, i) => (
              <TodoItem
                key={i}
                title={todo.title}
                summary={todo.summary}
                _id={todo._id}
                deleteTodo={this.handleDel}
              ></TodoItem>
            ))}
          </ul>
          <div className='card-body'>
            <center>
              <Link to='/add' className='btn btn-outline-primary'>
                Create Todo
              </Link>
            </center>
          </div>
        </div>
      </div>
    );
  }
}

export default TodoList;
