import React, { useState } from "react";
import { Redirect, Link } from "react-router-dom";
import { logout, createTodo } from "./../utils/apiFeatures";

const CreateTodo = ({ history }) => {
  const [values, setValues] = useState({
    title: "",
    summary: "",
    token: localStorage.getItem("jwt"),
    user: localStorage.getItem("user"),
    status: "",
    message: "",
    redirectTo: false,
  });

  const { title, summary, status, message, user, token, redirectTo } = values;

  const handleAddTodo = async () => {
    const response = await createTodo(user, token, title, summary);
    if (response.status === "success") {
      setValues({
        ...values,
        title: "",
        summary: "",
        message: "",
        status: response.status,
        redirectTo: true,
      });
    } else if (response.status === "fail") {
      setValues({
        ...values,
        status: response.status,
        message: response.message,
      });
    }
  };

  const showError = () => (
    <div
      className='alert alert-danger'
      style={{ display: status === "fail" ? "" : "none" }}
    >
      {message}
    </div>
  );

  const redirectUser = () => {
    if (redirectTo) {
      return <Redirect to='/' />;
    }
  };

  const handleChange = (name) => (event) => {
    event.preventDefault();
    setValues({ ...values, [name]: event.target.value });
  };

  const handleLogout = () => {
    history.push("/");
    logout();
  };

  const form = () => (
    <div className='card mt-5' style={{ width: "30rem" }}>
      <h6 className='card-header text-right'>
        <Link onClick={handleLogout} to='/'>
          Logout
        </Link>
      </h6>
      <h5 className='card-header text-center'>Add Todo</h5>
      {showError()}
      <div className='card-body'>
        <form>
          <div className='form-group'>
            <label>Title</label>
            <input
              value={title}
              type='text'
              className='form-control'
              placeholder='your title goes here...'
              onChange={handleChange("title")}
            />
          </div>
          <div className='form-group'>
            <label>Summary</label>
            <input
              value={summary}
              type='text'
              className='form-control'
              placeholder='your summary goes here...'
              onChange={handleChange("summary")}
            />
          </div>
        </form>
        <center>
          <button onClick={handleAddTodo} className='btn btn-primary'>
            Add
          </button>
        </center>
      </div>
    </div>
  );

  return (
    <div className='d-flex justify-content-center'>
      {form()}
      {redirectUser()}
    </div>
  );
};

export default CreateTodo;
