import React from "react";
import { Link } from "react-router-dom";

const Home = () => {
  const homeComponent = () => (
    <div className='jumbotron'>
      <h1 className='display-4'>Hello, User!</h1>
      <p className='lead'>
        This is a simple todo app, a simple jumbotron-style component for
        calling extra attention to featured content or information.
      </p>
      <hr className='my-4' />
      <p>
        It uses utility classNames for typography and spacing to space content
        out within the larger container.
      </p>
      <div>
        <Link to='/signup' className='btn btn-primary btn-lg mr-2'>
          Signup
        </Link>
        <Link to='/login' className='btn btn-primary btn-lg'>
          Login
        </Link>
      </div>
    </div>
  );
  return <div>{homeComponent()}</div>;
};

export default Home;
