import React, { useState } from "react";
import { Redirect, Link } from "react-router-dom";
import { login } from "./../utils/apiFeatures";

const LoginForm = () => {
  const [values, setValues] = useState({
    email: "",
    password: "",
    status: "",
    message: "",
    redirectTo: false,
  });

  const { email, password, status, message, redirectTo } = values;

  const handleChange = (name) => (event) => {
    setValues({ ...values, [name]: event.target.value });
  };

  const handleLogin = async (event) => {
    event.preventDefault();
    const response = await login(email, password);
    if (response.status === "success") {
      localStorage.setItem("jwt", response.token);
      localStorage.setItem("user", response.data.user._id);
      setValues({
        ...values,
        email: "",
        password: "",
        status: response.status,
        redirectTo: true,
      });
    } else if (response.status === "fail") {
      setValues({
        ...values,
        status: response.status,
        message: response.message,
      });
    }
  };

  const showError = () => (
    <div
      className='alert alert-danger'
      style={{ display: status === "fail" ? "" : "none" }}
    >
      {message}
    </div>
  );

  const redirectUser = () => {
    if (redirectTo) {
      return (
        <Redirect
          to={{
            pathname: "/",
            state: {
              reload: true,
            },
          }}
        />
      );
    }
  };

  const form = () => (
    <div className='card mt-5' style={{ width: "30rem" }}>
      <h6 className='card-header text-right'>
        <Link to='/signup'>Signup</Link>
      </h6>
      <h5 className='card-header text-center'>Login</h5>
      {showError()}
      <div className='card-body'>
        <form>
          <div className='form-group'>
            <label>Email address</label>
            <input
              type='email'
              value={email}
              className='form-control'
              placeholder='name@example.com'
              onChange={handleChange("email")}
            />
          </div>
          <div className='form-group'>
            <label>Password</label>
            <input
              type='password'
              value={password}
              className='form-control'
              onChange={handleChange("password")}
            />
          </div>
        </form>
        <center>
          <button
            type='submit'
            className='btn btn-primary text-center'
            onClick={handleLogin}
          >
            submit
          </button>
        </center>
      </div>
    </div>
  );

  return (
    <div className='d-flex justify-content-center'>
      {form()}
      {redirectUser()}
    </div>
  );
};

export default LoginForm;
