export const signup = async (name, email, password) => {
  const signUser = await fetch(
    `https://desolate-reef-21809.herokuapp.com/api/v1/users/signup`,
    {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ name, email, password }),
    },
  );

  return await signUser.json();
};

export const createTodo = async (user, token, title, summary) => {
  const addTodo = await fetch(
    `https://desolate-reef-21809.herokuapp.com/api/v1/todos`,
    {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify({ user, title, summary }),
    },
  );

  return await addTodo.json();
};

export const login = async (email, password) => {
  const logUser = await fetch(
    `https://desolate-reef-21809.herokuapp.com/api/v1/users/login`,
    {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ email, password }),
    },
  );

  return await logUser.json();
};

export const logout = () => {
  localStorage.removeItem("jwt");
  localStorage.removeItem("user");
};

export const deleteTodo = async (id, token) => {
  await fetch(`https://desolate-reef-21809.herokuapp.com/api/v1/todos/${id}`, {
    method: "DELETE",
    headers: {
      Authorization: `Bearer ${token}`,
    },
  });
  return true;
};

export const updateTodo = async (token, id, title, summary) => {
  const todo = await fetch(
    `https://desolate-reef-21809.herokuapp.com/api/v1/todos/${id}`,
    {
      method: "PATCH",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify({ title, summary }),
    },
  );
  return todo.json();
};

export const isAuthenticated = () => {
  if (localStorage.getItem("jwt") && localStorage.getItem("user")) {
    return true;
  }
};
