import React, { useState } from "react";
import { Link, Redirect } from "react-router-dom";
import { logout, updateTodo } from "./../utils/apiFeatures";

const EditTodo = ({ location, history }) => {
  const [values, setValues] = useState({
    title: location.state.prevTitle,
    summary: location.state.prevSummary,
    id: location.state.id,
    token: localStorage.getItem("jwt"),
    status: "",
    message: "",
    redirectTo: false,
  });

  const { id, title, summary, message, status, token, redirectTo } = values;

  const handleChange = (name) => (event) => {
    setValues({ ...values, [name]: event.target.value });
  };

  const handleLogout = () => {
    history.push("/");
    logout();
  };

  const handleUpdate = async () => {
    const response = await updateTodo(token, id, title, summary);
    if (response.status === "success") {
      setValues({
        ...values,
        title: "",
        summary: "",
        message: "",
        status: response.status,
        redirectTo: true,
      });
    } else if (response.status === "fail") {
      setValues({
        ...values,
        status: response.status,
        message: response.message,
      });
    }
  };

  const redirectUser = () => {
    if (redirectTo) {
      return <Redirect to='/' />;
    }
  };

  const showError = () => (
    <div
      className='alert alert-danger'
      style={{ display: status === "fail" ? "" : "none" }}
    >
      {message}
    </div>
  );

  const form = () => (
    <div className='card mt-5' style={{ width: "30rem" }}>
      <div>
        <h6 className='card-header text-right'>
          <Link onClick={handleLogout} to='/'>
            Logout
          </Link>
        </h6>
      </div>
      <h5 className='card-header text-center'>Edit Todo</h5>
      {showError()}
      <div className='card-body'>
        <form>
          <div className='form-group'>
            <label>Title</label>
            <input
              value={title}
              type='text'
              className='form-control'
              onChange={handleChange("title")}
            />
          </div>
          <div className='form-group'>
            <label>Summary</label>
            <input
              value={summary}
              type='text'
              className='form-control'
              onChange={handleChange("summary")}
            />
          </div>
        </form>
        <center>
          <button onClick={handleUpdate} className='btn btn-primary'>
            update
          </button>
        </center>
      </div>
    </div>
  );

  return (
    <div className='d-flex justify-content-center'>
      {form()}
      {redirectUser()}
    </div>
  );
};

export default EditTodo;
